import request from "../utils/request"; // 获取treelog列表

export var getAllTreeLog = function getAllTreeLog(query) {
  return request({
    url: 'get_all_tree_log',
    method: 'get',
    params: query
  });
}; // 删除treelog

export var deleteTreeLog = function deleteTreeLog(query) {
  return request({
    url: 'del_tree_log',
    method: 'get',
    params: query
  });
};